import React, { useState } from 'react'

export default function Home() {
    const [radio, setRadio] = useState("test1")
    return (
        <div>
            <h1>This is Main Page of this Application</h1>
            <h2>Radio Working</h2>
            <label> <input type="radio" value="test1" name="movie" onClick={(e) => setRadio(e.target.value)} />TEST1  </label>
            <label>  <input type="radio" value="test2" name="movie" onClick={(e) => setRadio(e.target.value)} />TEST2 </label>
            <p> Radio Selected {radio}</p>
        </div>
    )
}
