import React from 'react'

export default function User(props) {
   let {user}=props.match.params
   let userData = props.data.find(({name})=>name===user)

    return (
        <div>
            <h1>{userData.name}</h1>
    <p>Username : {userData.username}</p>
    <p>Contact: {userData.phone}</p>
    <h2></h2>
        </div>
    )
}
